import React, { useState } from "react";
import classNames from "classnames";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import { TooltipSvg } from "../TooltipSvg";
import { Input } from "@/components";
import "./styles.scss";


const ContractCardItem = ({ label, value, style = {} }) => {
  return (
    <div className="contract-card-item" style={style}>
      <div className="contract-card-item__label">{label}</div>
      <div className="contract-card-item__value">{value}</div>
    </div>
  );
};

export default function ContractCards(props)  {
  return (
    <div className="contract-cards">
      {props.tableData.map((row, idx) => (
        <Card
          className={classNames("contract-card", "contract-cards__item", {
            "contract-card--selected": !!row?.сhoosed_item,
          })}
          key={idx}
        >
          <CardContent>
            <div className="contract-card__checkbox checkbox bounce">
              <input
                type="checkbox"
                defaultChecked={!!row?.сhoosed_item}
                onChange={() =>
                  props.handleTableChange(
                    idx,
                    "сhoosed_item",
                    !row?.сhoosed_item
                  )
                }
              />
              <svg viewBox="0 0 21 21">
                <polyline points="5 10.75 8.5 14.25 16 6"></polyline>
              </svg>
            </div>
            <ContractCardItem
              label="Информация о договоре"
              value="№ 135 от 12.12.2020 Товарный кредит"
              style={{ width: "calc(100% - 30px)" }}
            />
            <div className="contract-card__row">
              <ContractCardItem
                label="Остаток задолженности"
                value="10 000 ₸"
              />
              <ContractCardItem
                label="Сумма для частично досрочного погашения"
                value="30 000 ₸"
              />
            </div>
            <div className="contract-card__row">
              <ContractCardItem
                label={
                  <div className="contract-card__tooltip-text">
                    <span>
                      Штрафы,
                      <br /> пени
                    </span>
                    <TooltipSvg message="Сумма штрафов и пени всегда включена в сумму общего платежа" />
                  </div>
                }
                value={<span className="contract-card__red">1 000 ₸</span>}
              />
              <ContractCardItem
                label={
                  <>
                    Переплата
                    <br /> по займу
                  </>
                }
                value="0 ₸"
              />
            </div>

            <ContractCardItem
              label={
                <>
                  К оплате
                  <TooltipSvg message="Поле доступно для редактирования. Не забудьте внести сумму с учетом штрафов или пени, в случае их наличия" />
                </>
              }
              value={
                <Input
                  value={row["input_item"]}
                  onChange={(e) =>
                    props.handleTableChange(idx, "input_item", e.target.value)
                  }
                  border
                  disabled={false}
                  style={{ height: "48px", fontSize: "20px" }}
                />
              }
            />
          </CardContent>
        </Card>
      ))}
    </div>
  );
};


