import React from "react";
import { NavLink } from "react-router-dom";
import SVG from "react-inlinesvg";
import StaticWeb from "public/staticWeb.svg";
import StaticMobile from "public/staticMobile.svg";
import Logo from "public/logo.svg";
import Phone from "public/phone.svg";
import ImgOfMobile from "public/imgOfMobile.svg";
import ImgOfMobileVersion from "public/imgOfMobileVersion.svg";
import HeaderOfMobile from "public/headerOfMobile.svg";

import "./style.scss";

function Layout(props) {
  const mobile = window.innerWidth < 600;
  return (
    <>
      {!mobile ? (
        <>
          <header>
            <SVG src={Logo} />
            <section className="news">Новинки</section>
            <section className="titleHeader">Отделения и банкоматы</section>
            <a>Интернет-банкинг</a>
            <section className="other">
              <section className="other__language">
                <div style={{ fontWeight: "700", color: "#3E9F1A" }}>Рус</div>
                <div style={{ fontWeight: "700", color: "#696969" }}>Каз</div>
              </section>
              <SVG src={Phone} />
              <section className="other__number">595</section>
              <section className="other__text">Бесплатный звонок</section>
            </section>
          </header>
          <nav>
            <section>Частным клиентам</section>
            <section>Для бизнеса</section>
            <section>Новости</section>
            <section>О банке</section>
            <section>Контакты</section>
            <section style={{ borderRight: "1px solid #4BA351" }}>FAQ</section>
          </nav>{" "}
        </>
      ) : (
        <SVG src={HeaderOfMobile} />
      )}

      {window.location.pathname !== "/result" && (
        <div className="webUnderHeader">
          <section className="webUnderHeader__box">
            <div className="webUnderHeader__box-title">
              Погашение кредитов и займов
            </div>
            <div
              className="webUnderHeader__box-text"
              style={{ marginTop: "20px" }}
            >
              Круглосуточно.
            </div>
            <div className="webUnderHeader__box-text">
              С любой банковской карты Казахстана
            </div>
          </section>
          <SVG src={mobile ? ImgOfMobileVersion : ImgOfMobile} />
        </div>
      )}

      {props.children}
    </>
  );
}

export { Layout };
