import React, { useEffect, useRef, useState } from "react";
import { Button } from "@/components";
import { nanoid } from "nanoid";
import { useDispatch, useSelector } from "react-redux";
import { sendOtp, otpVerify } from "@/services/requests";
import { setAlert } from "@/toolkit/alertSlice";
import { setCoborrower } from "@/toolkit/coborrowerSlice";
import { history } from "@/helpers";
import "./otpModal.scss";

export default function OtpModal(props) {
  const state = useSelector((state) => ({
    coborrower: state.default.coborrower,
  }));
  const { coborrower } = state;
  const dispatch = useDispatch();

  const [uniqueId, setUniqueId] = useState(null);

  const [otp1, setOTP1] = React.useState("");
  const [otp2, setOTP2] = React.useState("");
  const [otp3, setOTP3] = React.useState("");
  const [otp4, setOTP4] = React.useState("");

  const [counter, setCounter] = React.useState(119);
  const [error, setError] = React.useState(false);
  const [noCode, setNoCode] = React.useState(false);

  const otp1_ref = useRef(null);
  const otp2_ref = useRef(null);
  const otp3_ref = useRef(null);
  const otp4_ref = useRef(null);

  React.useEffect(() => {
    if (otp1.length > 0) {
      otp2_ref.current.focus();
    }
    if (otp2.length > 0) {
      otp3_ref.current.focus();
    }
    if (otp3.length > 0) {
      otp4_ref.current.focus();
    }
    if (otp4.length > 0) {
      otp4_ref.current.blur();
    }
    if (!uniqueId && !noCode) {
      let id = nanoid();
      setUniqueId(id);
      sendOtp({ requestId: id, phoneNumber: "87779654383" });
    }
  }, [otp1, otp2, otp3, otp4, uniqueId]);

  
  React.useEffect(() => {
    const timer =
      counter > 0 &&
      setInterval(
        () =>
          setCounter(
            parseInt(counter - 1).toString().length == 1
              ? `0${counter - 1}`
              : counter - 1
          ),
        1000
      );
    return () => clearInterval(timer);
  }, [counter]);

  const verifySms = async () => {
    const validateOtp = await otpVerify({
      requestId: uniqueId,
      phoneNumber: "87779654383",
      otp: `${otp1}${otp2}${otp3}${otp4}`,
    });

    if (validateOtp) {
      history.push("/payment");
    } else {
      setError(true);
      setOTP1("");
      setOTP2("");
      setOTP3("");
      setOTP4("");
      setCounter(119);
      otp1_ref.current.focus();
    }
  };

  if (noCode) {
    return (
      <div style={{ width: "90%", marginLeft: "5%" }}>
        <h2 className="t-l">Не пришёл код</h2>
        <div className="t-l">
          <div className="text-modal">
            Убедитесь, что на телефоне хороший сигнал сотовой связи и запросите
            код повторно
          </div>
        </div>
        <h2 className="t-l" style={{ marginTop: "30px" }}>
          Новый номер телефона
        </h2>
        <ul className="t-l" style={{ paddingLeft:"15px"}}>
          <li className="text-modal">
            Вы можете внести плановый платёж без СМСи без детализации по займу
          </li>
          <li className="text-modal" style={{ marginTop: "15px" }}>
            Обратитесь в отделение Банка с документом, удостоверяющим личность,
            чтобы обновить номер в договоре
          </li>
        </ul>
        <div className="d-g">
          <Button
            className="empty p-y-15 m-t-50 m-b-30 m-l-20"
            label="Оплатить без СМС"
            onClick={() => history.push("/payment-without-sms")}
          />
        </div>
      </div>
    );
  }

  let boxStyle = {
    fontSize: "1.5625rem",
    background: otp1 ? "#fff" : "#EFEFEF",
    border: `1px solid ${otp1 ? "#1A8C3A" : "none"}`,
  };

  if (error) {
    boxStyle = {
      fontSize: "1.5625rem",
      background: "#FFF4F4",
      border: `1px solid #FF0000`,
    };
  }
  return (
    <>
      {error ? (
        <>
          <h2 className="t-c" style={{ color: "red" }}>
            Ошибка !
          </h2>
          <h4 className="t-c">Не правильно введен код.</h4>
        </>
      ) : (
        <h2 className="t-c">Введите код из SMS</h2>
      )}

      <div className="t-c">
        <p style={{ color: "#000" }}>
          Код подтверждения отправлен на номер:{" "}
          <strong>{coborrower?.phone} +7 ••• ••• 43 83</strong>
        </p>
      </div>

      <div className="otp gg-20 m-t-55 m-b-35 p-x-30">
        <div className="input">
          <input
            type="tel"
            className="input__section border p-y-30 t-c"
            value={otp1}
            style={boxStyle}
            onChange={(e) => {
              setError(false);
              setOTP1(e.target.value);
            }}
            autoFocus={!otp1}
            ref={otp1_ref}
          />
        </div>
        <div className="input">
          <input
            type="tel"
            className="input__section border p-y-30 t-c"
            value={otp2}
            style={boxStyle}
            onChange={(e) => setOTP2(e.target.value)}
            ref={otp2_ref}
          />
        </div>
        <div className="input">
          <input
            type="tel"
            className="input__section border p-y-30 t-c"
            value={otp3}
            style={boxStyle}
            onChange={(e) => setOTP3(e.target.value)}
            ref={otp3_ref}
          />
        </div>
        <div className="input">
          <input
            type="tel"
            className="input__section border p-y-30 t-c"
            value={otp4}
            style={boxStyle}
            onChange={(e) => setOTP4(e.target.value)}
            ref={otp4_ref}
          />
        </div>
      </div>
      <div className="t-c">
        {counter != 0 ? (
          <div style={{ display: "flex", justifyContent: "center" }}>
            <div
              style={{
                fontFamily: "Good Pro",
                fontWeight: "bold",
                color: "#8C8c8C",
                background: "#F5F5F5",
                padding: "5px",
                width: "max-content",
              }}
            >
              Запросить код через{" "}
              <strong style={{ color: "red" }}>
                {`0${Math.trunc(counter / 60)}`}:
                {counter - Math.trunc(counter / 60) * 60} сек
              </strong>
            </div>
          </div>
        ) : (
          <a
            className="link black"
            style={{ color: "black" }}
            onClick={() => {
              let id = nanoid();
              setUniqueId(id);
              setOTP1("");
              setOTP2("");
              setOTP3("");
              setOTP4("");
              setCounter(119);
              otp1_ref.current.focus();
              sendOtp({ requestId: id, phoneNumber: "87779654383" });
            }}
          >
            ЗАПРОСИТЬ КОД
          </a>
        )}
      </div>

      <div style={{ display: "flex", justifyContent: "center" }}>
        <Button
          className="success p-y-15 m-t-50 m-b-30"
          label="Далее"
          onClick={verifySms}
          disabled={!(otp1 && otp2 && otp3 && otp4)}
        />
      </div>
      <div className="t-c" onClick={() => setNoCode(true)}>
        <p style={{ textDecoration: "underline" }}>
          Проблемы с получением кода?
        </p>
      </div>
    </>
  );
}
